<template>
    <div class="flex flex-col items-center">
        <h1>My Transactions</h1>
        <div v-for="( transaction, i ) in getTransactions" class="transaction flex justify-between" :key="i">
          <div class="w-20">
            <PlusSign v-if="transaction.type === 'credit'" class="text-green-500"/>
            <MinusSign v-else class="text-red-600"/>
          </div>
          <div>{{ transaction.description.substring(0, 20) }}</div>
          <div class="flex">
            <div class="mr-4">{{ transaction.amount | formatMoney }}</div>
            <button @click.prevent="removeTransaction(i)">X</button>
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import MinusSign from '@/components/ui/MinusSign'
import PlusSign from '@/components/ui/PlusSign'
export default {
  name: 'Index',
  components: {
    MinusSign,
    PlusSign
  },
  computed: {
    ...mapGetters({
      getTransactions: 'getTransactions'
    })
  },
  methods: {
    removeTransaction (index) {
      this.$store.dispatch('showModal')
        .then(() => {
          this.$store.dispatch('removeTransaction', index)
        })
    }
  }
}
</script>